.order-sent {
    margin-top: 50px;;
    text-align: center;
    color: #888;
}
.order-sent .emoji-celebration {
    font-size: 3rem;
}
.order-sent h1 {
    margin: 10px 0 20px;
}
.order-sent h2 {
    margin-bottom: 20px;
    font-size: 1.2rem;
    font-weight: normal;
    padding: 0 20px;
}
.order-send-client-name {
    font-weight: bolder;
}
.order-sent-link {
    bottom: 20px;
    position: absolute;
    width: 100%;
    display: flex;
    flex-flow: column wrap;

    font-size: 1rem;
}
.order-sent-link a {
    text-decoration: none;
    color: #888;
}
.order-sent-link a:first-child {
    margin-bottom: 20px;
}
