.header {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 25px 0 150px;
    position: relative;
}
.header-content {
    position: absolute;
    background: rgba(0,0,0,.3);
}
.header img {width: 100px;}
.header h2 {
    margin: 30px 0;
    text-align: center;
    color: #166668;
    font-size: 1.9rem;
    text-shadow: 0px 0px 2px rgba(0,0,0,.3);
}
.header p {
    padding: 0 20px;
    font-size: 1.2rem;
    text-align: center;
    color: #555;
    text-shadow: 0px 0px 1px rgba(0,0,0,.3);
}
.header div {
    width: 5px;
    height: 50px;

    position: absolute;
    bottom: 0;

    background: #38c4ac;
    transform: rotate(-50deg);
    left: calc(50% - 18px);
    border-radius: 50px;

    margin-bottom: 40px;
}
.header div:last-child {
    transform: rotate(50deg);
    left: calc(50% + 18px);
}

/*  */
.plans {
    margin-bottom: 50px;
}
.plans h1 {
    font-family: 'Open Sans Condensed', sans-serif;
    text-align: center;
    font-size: 2.4rem;
    font-weight: bold;
    color: #0d4d4e;
    margin-bottom: 20px;
}
.plans p {
    font-size: 1.2rem;
}

/*  */
.plan-info {
    display: flex;
    flex-direction: column;
    align-items: center;

    box-shadow: 0px 0px 10px #ccc;
    margin: 0 10px;
    padding: 15px;

    border-radius: 10px;
}
.plan-info h3 {
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Open Sans Condensed', sans-serif;
}
.plan-info p.plan-price {
    margin: 20px 0;
}
.plan-info p.plan-price span {
    font-size: .7rem;
}
.plan-info .plan-info-submit {
    width: 100%;
    padding: 30px 10px;
    
    border: none;
    border-radius: 4px;
    background: #38c4ac;
    color: #fff;
    font-weight: bold;
    font-size: 1.2rem;
    text-decoration: none;
    text-align: center;
}

/*  */
.plan-items {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.plan-items p {
    font-size: 1.2rem;
    font-weight: bold;
}
.plan-items ul {
    display: flex;
    list-style: none;
    width: 100%;
    margin: 20px 0;;
}
.plan-items ul li{
    width: 25%;
    padding: 15px 0;
    font-size: 1.2rem;
    text-align: center;
    border: solid 1px #ddd;
    border-right: none;
    background: transparent;
}
.plan-items ul li:last-child {
    border-right: solid 1px #ddd;
    border-top-right-radius: 5px;
}
.plan-items ul li:first-child {
    border-bottom-left-radius: 5px;
}
.plan-items .planActive {
    background: #38c4ac;
    color: #fff;
}

/*  */
.how-it-works {
    margin: 60px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.how-it-works-item {
    padding: 20px;
    margin: 30px 0;
    width: 100%;
}
.how-it-works-item ul {
    list-style: none;
}
/* .how-it-works-item .emoji {
    font-size: 5rem;
    text-shadow: 0px 0px 20px rgba(0,0,0,.3);
} */
.how-it-works-item h3 {
    font-size: 1.5rem;
    text-transform: uppercase;
    margin-bottom: 10px;
}
.how-it-works-item p {
    font-size: 1.3rem;;
}
.how-it-works-item img {
    width: 150px;
    margin-bottom: 10px;
    border-radius: 50%;
    box-shadow: 0px 0px 20px rgba(0,0,0,.3);
}


/*  */
.meal-style {
    width: 100vw;
    height: 400px;

    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;

    text-align: center;
}
.meal-style-bg {
    width: 100vw;
    height: 100%;

    background: url('../img/meals-type-home.jpg') no-repeat;
    background-size: cover;
    background-attachment: fixed;

    filter: blur(5px);
    -webkit-filter: blur(5px);
}
.meal-style-text {
    height: 100%;

    position: absolute;
    top: 0;
    z-index: 2;

    display: flex;
    flex-direction: column;
    justify-content: space-around;

    padding: 30px;

    color: #38c4ac;
    text-shadow: 0 0 20px #000;
}
.meal-style-text h2 {
    font-size: 1.5rem;
    font-weight: normal;
}
.meal-style-text span {
    width: 100%;
    display: block;
    font-weight: bolder;
}
.meal-style-text span:first-child { margin-top: 20px;}
.meal-style-text p {
    font-size: 1.1rem;
}

/* ------------- FOOTER ------------- */
.home-footer {
    width: 100%;
    padding: 30px;
    padding-top: 0px;
    
    font-size: 1.5rem;
    color: #38c4ac;
    text-shadow: 0 0 1px rgba(0,0,0,.3);
    text-align: center; 
}