.checkout-form {
    display: flex;
    flex-flow: column wrap;
}
.checkout-form .checkout-form-input {
    padding: 20px 0px;
    margin: 5px auto;

    font-size: 1.4rem;
    color: #3bb2b8;
    border: none;
    outline:none;
    text-align: center;
    box-shadow: 0 0 2px #3bb2b888;
    border-radius: 5px;
}
.checkout-form .checkout-form-input::placeholder,
.checkout-form .checkout-form-input::-webkit-input-placeholder {
    color: #3bb2b888;
}

.checkout-form input[type=submit] {
    padding: 20px 0;
    margin-top: 5px;
    
    border: none;
    outline: none;
    background-color: #3bb2b8;
    color: #fff;
    font-weight: bold;
    font-size: 1.5rem;
    border-radius: 5px;
}

.checkout-form .phoneErrorMessage {
    margin-bottom: 20px;
    padding: 5px;
    
    font-size: 1rem;
    color: #e15554;
    background-color: #e1555422;
    border-radius: 4px;
}
