/* HELPERS */
.font-red {color: #fd5e53;}
.font-cyan {color: #00d1a4;}
.font-grape {color: #72259e;}

.transformX {
    transform: translateX(-30px);
}

/* -------------------------- MEALS CHOISE HEADER -------------------------- */
.meal-choise-header {
    display: flex;
    align-items: center;

    padding: 15px 10px; 

    font-size: 1.3rem;
    box-shadow: 0 0 4px rgba(0,0,0,.2);
}
.meal-choise-header p {
    flex-grow: 1;
    text-align: center;
    color: #777;
}
.meal-choise-header-icon-back {
    font-size: 2rem;
    color: #bbb;
}
/* -------------------------- MEALS WRAP -------------------------- */
.meals-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 15px;
    margin-bottom: 130px;
}

.meal {
    width: 95%;

    display: flex;
    align-items: center;
    position: relative;
    padding: 7px;
    
    box-shadow: 0 0 10px rgba(0,0,0,.2);
    margin-bottom: 15px;
    border-radius: 20px;
}
.meal img {
    width: 40%;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0,0,0,.2);
}
.meal-info {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-self: stretch;
}
.meal-ingredients {
    display: flex;
    align-content: center;
    align-items: center;
    flex-grow: 1;
    padding: 5px 5px 0;
    text-align: center;
    font-size: 1rem;
    color: #777;
    font-style: italic;
}
.meal-btn-add {
    width: fit-content;
    align-self: flex-end;

    display: flex;
    justify-content: center;
    align-items: center;

    padding: 10px 15px;
    
    background: #33b9b2;
    border: none;
    color: #fff;
    font-size: 1.3rem;
    border-radius: 20px;
    
    transition: all .2s;
}

 /* -------------------------- SELECTED MEALS -------------------------- */
.selected-meals {
    position: fixed;
    bottom: 0;
    width: 100%;

    background-color: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,.4);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.selected-meals-header {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 10px;
}
.selected-meals-header div {
    flex-grow: 1;
}
.selected-meals-left span {
    font-size: 1.2rem;
    font-weight: bolder;
}
.selected-meals-header button {
    width: fit-content;
    padding: 15px 30px;
    
    background: #72259e;
    border: none;
    color: #fff;
    font-size: 1.3rem;
    border-radius: 8px;
    
    transition: all .1s;
}
.selected-meals-header button[disabled] {
    opacity: 0.4;
}
.selected-meals-header button:active {
    background: #72259e;
    transform: translate3d(-10px, 10px, 10px);
}

/* carousel wrapper */
.carousel-wrapper {
    width: 100%;
    display: flex;

    padding-top: 5px;
    margin-right: 20px;

    overflow: scroll;
}
.carousel-item {
    min-width: 180px;
    max-width: 180px;
    height: 50px;

    display: flex;
    align-items: center;
    position: relative;

    margin: 10px;
    padding: 3px;

    box-shadow: 0 0 40px rgba(0, 0, 0, 0.5);
    border-radius: 4px;
    background-color: #fff;
}
.carousel-item img {
    width: 45px;
    
    border-radius: 50%;
    /* box-shadow: 0 0 2px rgba(0, 0, 0, 0.75); */
}
.carousel-item p {
    font-size: .8rem;
    margin-left: 5px;
    line-height: 1.2;
}
