/* -------------------------- MODAL -------------------------- */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);

    opacity: 0;
    transition: opacity 300ms ease-in-out;
}
.modal-overlay--after-open{
    opacity: 1;
}
.modal-overlay--before-close{
    opacity: 0;
}

.modal-content-meal {
    position: absolute;
    width: 85vw;
    height: 80vh;
    left: 7.5vw;
    top: 10vh;
    padding: 5vw;
    padding-top: 2.5rem;
    

    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    outline: none;
    border-radius: 4px;

    opacity: 0;
    transition: all 500ms ease-in-out;
    transform: translateY(-40%);
}
.modal-content-checkout {
    position: absolute;
    width: 92vw;
    height: 90vh;
    left: 4vw;
    top: 5vh;
    padding: 5vw;
    padding-top: 20px;
    

    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    outline: none;
    border-radius: 4px;

    opacity: 0;
    transition: all 500ms ease-in-out;
    transform: translateY(-40%);
}
.modal-content--after-open{
    opacity: 1;
    transform: translateY(0);
}
.modal-content--before-close{
    opacity: 0;
    transform: translateY(-50%);
}
.modal-close-icon {
    position: absolute;
    top: 4px;
    right: 4px;

    font-size: 2.4rem;
    color: #999;
}

/* MODAL-MEAL INTERNAL CONTENT */
.modal-meal-internal-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.modal-meal-internal-content img {
    width: 90%;
    border-radius: 50%;
    box-shadow: 0 0 10px rgba(0,0,0,.2);
}
.modal-meal-internal-content button {
    width: 70%;
    padding: 10px 30px;
    margin: 0 auto;
}
.modal-meal-internal-content p {
    margin: 20px 0;

    text-align: center;
    font-size: 1.2rem;
    color: #777;
}

/* MODAL-CHECKOUT INTERNAL CONTENT */
.modal-checkout-internal-content {
    width: 100%;
    color: #777;
    text-align: center;
}
.modal-checkout-internal-content header {
    padding-bottom: 20px;
    margin-bottom: 20px;

    font-size: 1.3rem;
    border-bottom: 1px solid #ccc;
}
.modal-checkout-internal-content header p:last-child {
    font-size: 1rem;
}
.modal-checkout-internal-content header .emoji-smile {
    font-size: 2.5rem;
}
.modal-checkout-internal-content-form p {
    font-size: 1.3rem;
}